import React from 'react'
import './coin.css';

const Coin = ({name,image,price,volume,symbol,priceChg,marketCap})=>{
    return (
        <div className="coin-container">
            <div className="coin-row">
                <div className="coin">
                    <img src={image} alt = "crypto"></img>
                    <h1>{name}</h1>
                    <p className = "coin-symbol">{symbol}</p>
                </div>
                <div className="coin-data">
                    <p className="coin-price">${price}</p>
                    <p className="coin-volume">${volume.toLocaleString()}</p>
                    {priceChg<0? (<p className = "price-change red">{priceChg.toFixed(2)}%</p>):(<p className = "price-change green">{priceChg.toFixed(2)}%</p>) }
                    <p className = "coin-market-cap"> Mkt Cap : {marketCap.toLocaleString()} </p>
                </div>
            </div>
        </div>
    )
} 

export default Coin;
